.ErrorBox
{
    width: 760px;
    margin: auto auto;
    border: 1px solid white;
    border-radius: 15px;
    background-color: rgba(white,0.4);
    backdrop-filter: blur(2px);
    height: 60vh;
    display: flex;
    flex-direction: column;
    padding: 50px;
    justify-content: space-evenly;
    box-sizing: border-box;

    h1
    {
        text-align: center;
        font-size: 36px;
        font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        margin: 0px;
    }

    h2
    {
        text-align: center;
    }

    button 
    {
        padding: 15px 0px;
        background: linear-gradient(90deg, orangered, rgb(255, 145, 0));
        font-size: 1.2rem;
        color: white;
        font-weight: 1000;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        border-radius: 15px;
    }

    button:hover
    {
        background: linear-gradient(90deg,darken(orangered,1),rgb(255, 102, 0));
        font-size: 1.2rem;
    }
}